import { setStorage, getStorage } from '@/assets/js/units.js';
import getWeb3 from '@/assets/js/getWeb3.js'
import DOGES from '@/assets/coin_abi/DOGES.json'
import LP from '@/assets/coin_abi/LP.json'
import DOGE_pool from '@/assets/pool_abi/pool_DOGE.json'
const state = {
  /*存储用户信息全局变量*/
  // dogBalance: JSON.parse(getStorage('dogBalance') || 0),
  dogCoinContract: JSON.parse(getStorage('dogCoinContract') || null),
  // lpBalance: JSON.parse(getStorage('lpBalance') || 0),
  lpCoinContract: JSON.parse(getStorage('lpCoinContract') || null),
  poolContract: JSON.parse(getStorage('poolContract') || null),
  dogesInfo: JSON.parse(getStorage('dogesInfo') || null),
}

const getters = {
  /*获取用户信息*/
  getIsApprove: function (state) {
    let isApprove = null;
    if (state.isApprove) {
      isApprove = state.isApprove;
    } else {
      isApprove = JSON.parse(getStorage('isApprove') || null);
    }
    return isApprove
  },

}

const mutations = {
  // set_dogBalance: (state, data) => {
  //   state.dogBalance = data;
  // },
  set_dogCoinContract: (state, data) => {
    state.dogCoinContract = data;
  },
  // set_lpBalance: (state, data) => {
  //   state.lpBalance = data;
  // },
  set_lpCoinContract: (state, data) => {
    state.lpCoinContract = data;
  },
  set_poolContract: (state, data) => {
    state.poolContract = data;
  },
  set_dogesInfo: (state, data) => {
    state.dogesInfo = data;
  },
}

const actions = {

  // 合约矿池
  add_DOGESpool: ({ commit }, data) => {
    let info = {};
    getWeb3.Init(DOGE_pool, '0x4689b256618f43a2cea8be6b03c19043e42a1bcb', (contract, addr, balance) => {
      commit('set_poolContract', contract);
      info.account = addr
      info.HTbalance =  web3.utils.fromWei(balance, "ether")
      info.address = '0x4689b256618f43a2cea8be6b03c19043e42a1bcb'
      contract.methods.getMiningInfo(addr).call().then((res) => {
        info.info = res[0];
        info.inviteCode = res[1];
        info.referrer = res[2];
        info.circleAddr = res[3];
        var oldArr = res[3].toLocaleLowerCase();
					var hasIndexOf = oldArr.indexOf(addr);
					if (hasIndexOf == 0) {
						info.isMaster = true;
					}else{
            info.isMaster = false;
          }
					var url = window.location.origin;
          var code = res[2] ||  res[1];
          info.link =  url
          // if(code){
          //   info.link =  url+'/#/?code='+code
          // }else{
          //   info.link =  url
          // }
					
        commit('set_dogesInfo', info);
      })
    })
  },

    // 获取交易对合约对象
    add_lpCoinContract: ({ commit }, data) => {
      getWeb3.Init(LP, '0x85db145bd83f5c7cc2bfe97ba3316646e112b56f', (contract, addr, balance) => {
        commit('set_lpCoinContract', contract);
        // contract.methods
        //   .balanceOf(addr)
        //   .call()
        //   .then((res) => {
        //     commit('set_lpBalance', web3.utils.fromWei(res, "ether"))
        //   });
  
      })
    },

  // 获取DOG币合约对象
  add_dogCoinContract: ({ commit }, data) => {
    getWeb3.Init(DOGES, '0xf0C879feA59986E22FDeAD7D21fD3a3CCefa1A59', (contract, addr, balance) => {
      commit('set_dogCoinContract', contract);
      // contract.methods
      //   .balanceOf(addr)
      //   .call()
      //   .then((res) => {
      //     commit('set_dogBalance', web3.utils.fromWei(res, "ether"))
      //   });

    })
  },
}
export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}




