      <div class="web marginB30">
        <div class="titBg"><h1 class="lineCorle">Why is it static?</h1></div>
        <p class="text-p">Static rewards solve many problems. <span style="color:#f5ca47"> First, the reward amount depends on the number of tokens being traded. This mechanism is designed to relieve some of the downward pressure on tokens caused by early adopters selling their tokens after planting insanely high APYS. Second, the reflection mechanism encourages holders to hang up their tokens for a higher rebate based on the percentage executed and depending on the total tokens held by the owner.</span></p>
      </div>

       <div class="web" style="margin-top:50px">
        <div class="titBg"><h1 class="lineCorle">Manual burns</h1></div>
        <p class="text-p">The purpose of CBN is to implement a burn strategy that is beneficial and rewarding for personnel engaged in long-term operations. In addition, the reading on our website shows the total amount of combustion, which makes it more transparent in determining the current cycle supply at any given point in time.</p>
      </div>

     <div class="pet " id="pet2" >
      <div class="web">
        <div class="titBg"><h1 class="lineCorle">Automatic Liquidity Pool</h1></div>
        <p>The automatic LP is CBN's secret weapon. In this case, we have a feature that has a double benefit for the holder. First, the contract takes tokens from sellers and buyers and adds them to the LP, thus establishing a solid price floor. Second, punishment is an anti-arbitrage mechanism, ensuring that large amounts of CBN are rewarded to holders. In theory, the added LP creates stability from the provided LP by adding a tax to the overall liquidity of the token, thereby increasing the overall LP of the token and supporting the token's price floor. This differs from the burning capabilities of other reflection tokens, which benefit only in the short term from the reduced availability of licenses. With the addition of the CBN token LP, price stability reflects this feature, which has the advantages of a solid base price and bracket seat cushion. The goal is to prevent the whale from having a big drop later in the game when it decides to sell its token, which prevents price fluctuations as if there were no automatic LP feature. All of this is to alleviate some of the trouble we've had with the current DeFi reflection token. For these reasons, we are confident that this model and protocol will take precedence over outdated reflection tokens.</p>
      </div>
