<script type="es6">
import "element-ui/lib/theme-chalk/display.css";
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      lpBalance: 0,
      coinBalance: 0,
      isApprove: false,
      stakeAmount: 0,
      dialogTableVisible: false,
      hmdApprove: false,
      joinEstablish: "",
      creationCode: "",
      dogeApprove: false,
      drawer: false,
      direction: "ltr",
    };
  },
  computed: {
    ...mapState("main", [
      // "dogBalance",
      "dogCoinContract",
      // "lpBalance",
      "lpCoinContract",
      "poolContract",
      "dogesInfo",
    ]),
  },
  methods: {
    ...mapActions("main", [
      "add_DOGESpool",
      "add_lpCoinContract",
      "add_dogCoinContract",
    ]),
    randomNum: function () {
      var code = "";
      for (var i = 1; i <= 6; i++) {
        const num = Math.floor(Math.random() * 10);
        code += num;
      }
      this.creationCode = code;
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
      localStorage.setItem("lang", lang);
    },

    // 复制成功时的回调函数
    onCopy(e) {
      this.$message({
        showClose: true,
        message: "Copy success",
        type: "success",
      });
    },
    // 复制失败时的回调函数
    onError(e) {
      this.$message({
        showClose: true,
        message: "Copy the failure",
        type: "error",
      });
    },
    open2() {
      this.$alert("这是一段内容", "标题名称", {
        confirmButtonText: "确定",
        callback: (action) => {
          this.$message({
            type: "info",
            message: `action: ${action}`,
          });
        },
      });
    },
    establish() {
      this.$message({
        showClose: true,
        message: this.$t("message.warning"),
        type: "warning",
      });
      return;
      var _this = this;
      var account = _this.dogesInfo.account;
      var value = _this.creationCode;
      if (!value) {
        this.$message({
          showClose: true,
          message: _this.$t("message.noCode"),
          type: "error",
        });
        return;
      }

      if (Number(_this.coinBalance) < 10) {
        this.$message({
          showClose: true,
          message: _this.$t("message.noDoges"),
          type: "error",
        });
        return;
      }

      var loading = _this.$loading({
        lock: true,
        text: "Padding....",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.5)",
      });

      _this.poolContract.methods
        .isUsed(value)
        .call()
        .then((result) => {
          if (result) {
            this.$message({
              showClose: true,
              message: _this.$t("message.changeCode"),
              type: "error",
            });
            loading.close();
          } else {
            this.poolContract.methods
              .cretaePool(value)
              .send({ from: account })
              .then((res) => {
                console.log(res);
                loading.close();
                this.$message({
                  showClose: true,
                  message: "success",
                  type: "success",
                });
                _this.getLpBalance();
                _this.getDOGBalance();
                _this.add_DOGESpool();
              })
              .catch((err) => {
                loading.close();
                _this.$message({
                  showClose: true,
                  message: err.message,
                  type: "error",
                });
                console.log(err);
              });
          }
        })
        .catch((err) => {
          loading.close();
          _this.$message({
            showClose: true,
            message: err.message,
            type: "error",
          });
          console.log(err);
        });
    },
    open() {
      this.$message({
        showClose: true,
        message: this.$t("message.warning"),
        type: "warning",
      });
      return;
      var _this = this;
      var account = _this.dogesInfo.account;
      this.$prompt(_this.$t("message.enter"), "Join mine pool", {
        confirmButtonText: "OK",
        cancelButtonText: "Cancel",
        inputPlaceholder: _this.joinEstablish,
      })
        .then(({ value }) => {
          value = value || _this.joinEstablish;
          if (!value) {
            this.$message({
              showClose: true,
              message: _this.$t("message.enter"),
              type: "error",
            });
            return;
          }

          var loading = _this.$loading({
            lock: true,
            text: "Padding....",
            spinner: "el-icon-loading",
            background: "rgba(0, 0, 0, 0.5)",
          });

          _this.poolContract.methods
            .isUsed(value)
            .call()
            .then((result) => {
              if (!result) {
                this.$message({
                  showClose: true,
                  message: _this.$t("message.noPool"),
                  type: "error",
                });
                loading.close();
              } else {
                this.poolContract.methods
                  .join(value)
                  .send({ from: account })
                  .then((res) => {
                    console.log(res);
                    loading.close();
                    this.$message({
                      showClose: true,
                      message: "success",
                      type: "success",
                    });
                    _this.getLpBalance();
                    _this.getDOGBalance();
                    _this.add_DOGESpool();
                  })
                  .catch((err) => {
                    loading.close();
                    _this.$message({
                      showClose: true,
                      message: err.message,
                      type: "error",
                    });
                    console.log(err);
                  });
              }
            })
            .catch((err) => {
              loading.close();
              _this.$message({
                showClose: true,
                message: err.message,
                type: "error",
              });
              console.log(err);
            });
        })
        .catch(() => {
          loading.close();
          this.$message({
            type: "info",
            message: "Cancel the input",
          });
        });
    },
    //取消抵押
    withdraw: function () {
      this.$message({
        showClose: true,
        message: this.$t("message.warning"),
        type: "warning",
      });
      return;
      var _this = this;
      if (_this.dogesInfo == null) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.nolLinked"),
          type: "error",
        });
        return;
      }

      var account = _this.dogesInfo.account;
      var myzy = web3.utils.fromWei(_this.dogesInfo.info[6], "ether");
      if (myzy <= 0) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noZy"),
          type: "error",
        });
        return;
      }
      var loading = _this.$loading({
