      if (this.dogesInfo == null) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.nolLinked"),
          type: "error",
        });
        return;
      }

      var account = _this.dogesInfo.account;
      var code = _this.dogesInfo.inviteCode || _this.dogesInfo.referrer;
      if (!_this.isApprove) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noApprove"),
          type: "error",
        });
        return;
      }

      if (!code || code == "--") {
        code = _this.$route.query.invite
          ? _this.$route.query.invite.toString()
          : 1;
      }
      if (Number(_this.stakeAmount) > Number(_this.lpBalance)) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noLp"),
          type: "error",
        });
        return;
      }
      if (Number(_this.dogesInfo.HTbalance) <= 0) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noGas"),
          type: "error",
        });
        return;
      }
      if (!code || code == "--") {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noCode2"),
          type: "error",
        });
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$message({
          showClose: true,
          message: _this.$t("message.noLp"),
          type: "error",
        });
        return;
      }
      var loading = _this.$loading({
        lock: true,
        text: "Padding....",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.5)",
      });
      // var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.poolContract.methods
        .investIn(code, web3.utils.toWei(_this.stakeAmount, "ether"))
        .send({ from: account })
        .then((res) => {
          console.log(res);
          loading.close();
          this.$message({
            showClose: true,
            message: "success",
            type: "success",
          });
          _this.getLpBalance();
          _this.getDOGBalance();
          _this.add_DOGESpool();

          _this.stakeAmount = 0;
        })
        .catch((err) => {
          loading.close();
          _this.$message({
            showClose: true,
            message: err.message,
            type: "error",
          });
          console.log(err);
        });
    },
    // 是否授权
    isApproveFun: function () {
      if (this.dogesInfo) {
        var account = this.dogesInfo.account;
        var address = this.dogesInfo.address;

        this.lpCoinContract.methods
          .allowance(account, address)
          .call()
          .then((res) => {
            var a = web3.utils.fromWei(res, "ether");
            if (a > 0) {
              this.isApprove = true;
            }
            console.log(a);
          })
