  border: none;
  padding: 10px 20px;
  color: #fff;
  font-size: 18px;
background: linear-gradient(0deg, #155121 0%, #1F5D21 10%, #50CF13 100%);
  float: right;
  border-radius: 20px;
}
.con .content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.con .content input {
  height: 50px;
  border: 0;
  background: #e9eefc;
  width: 100%;
  box-sizing: border-box;
  padding: 0 10px;
  border-radius: 10px;
  font-size: 16px;
  color: #000000;
}
.con p {
  margin-bottom: 10px;
}
.con .popfooter {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.con .popfooter .btn {
  height: 45px;
  width: 40%;
  color: #fff;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
}

.con .popfooter .cancel {
  background: #62698e;
}

.con .popfooter .confirm {
  background: #007bec;
}

.sc {
  position: absolute;
  padding: 0 10px;
  background: #ff861e;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: #fff;
  border-radius: 0 10px 10px 0;
  right: 20px;
}
.hidden-sm-and-show {
  display: none;
}
.drawerList a {
  display: block;
  text-align: center;
  font-size: 16px;
  color: #4e4f57;
  width: 95%;
  margin: 20px auto;
  padding: 10px 0 20px;
  border-bottom: 1px solid #f1f2f3;
}
.hbox {
  height: 160px;
}
.hbox p{
  margin-bottom: 5px;
}
@media only screen and (max-width: 767px) {
  .home{
        background: url(../../assets/img/min-bg.png) no-repeat #05211f;
        background-size:100% ;
  }
  .pet .bg-l{
  margin: 10px 0;
      background-size: 100% 100%;
}
  .web {
    width:92%;
    margin: 0 auto;
    overflow: hidden;
  }

  .hidden-sm-and-show {
    display: block;
  }
  .head a {
    float: right;
  }
  .introduce .title{
    line-height: 1em;
    font-size: 2.2em;
    margin-bottom: 20px;
  }
  .gou{height:20em}
  .gou{
    width: 90%;
  }
  .introduce {
    padding-top: 2rem;
  }
  .introduce .title {
    top: -1.5rem;
  }
  /* .introduce h2{margin: 0.8em auto;} */
  .traffic div p {
    font-size: 18px;
  }
  .traffic .float-box {
    margin-left: 12px;
  }
  .btnbox a {
    padding: 12px 15px;
    margin: 0 10px;
    font-size: 1rem;
  }
  .food h1 {
    font-size: 34px;
    margin-bottom: 0px;
  }
  .food .el-row:first-child {
    margin-bottom: 50px;
  }
  .earnings,
  .info,
  .invest {
    min-height: auto;
    background: url(../../assets/img/insvtBg.png) no-repeat #062f2c;
    background-size:100% ;
    /* padding:20px; */
  }
  .trad {
    background-size: 100%;
  }
}
@media (min-width: 768px) and (max-width: 992px) {
  .web {
    width: 95%;
    margin: 0 auto;
    overflow: hidden;
  }
  .hidden-sm-and-show {
    display: block;
  }
  .head a {
    float: right;
  }

}
@media (min-width: 992px) and (max-width: 1142px) {
  .web {
    width: 95%;
    margin: 0 auto;
    overflow: hidden;
  }
}
</style>



